.nav {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    padding: 0 2% 0 13%;
    -ms-flex-align: center;
        align-items: center;
    background-color: #373944;
    color: #e7853c;
}

.nav-link-wrapper {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
}

.title {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    font-family: 'Exo', sans-serif;
}

.links > a {
    color: #e7853c;
    padding: 10px;
    text-decoration: none;
    -webkit-transition: color 1s;
    -o-transition: color 1s;
    transition: color 1s;
    font-family: 'Encode Sans Expanded', sans-serif;
}

.links > a:hover {
    color: white;
}

.nav-link-wrapper > div > button {
    cursor: pointer;
    margin-left: 10px;
    font-family: 'Encode Sans Expanded', sans-serif;
    font-size: 15px;
}


